.background {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  bottom: 0;
  left: 0; }
  .background canvas {
    filter: contrast(120%) grayscale(0.3);
    background-color: transparent;
    height: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    transform: translateZ(0);
    pointer-events: none; }

.home {
  padding: 4em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  height: 100vh;
  box-sizing: border-box; }
  .home h1 {
    position: relative;
    font-weight: 200;
    letter-spacing: 4px;
    font-size: 2.5em;
    text-shadow: 0px 0px 3px #272727; }

.social {
  position: relative;
  z-index: 1; }
  .social img {
    width: 3.5em;
    margin-right: 0.4em;
    margin-top: 1em;
    background-color: white; }
    .social img[type='github'] {
      background-color: #272727; }
    @media (max-width: 400px) {
      .social img {
        width: calc(25vw - 2.4rem); } }

.contact {
  position: fixed;
  right: 4rem;
  bottom: 4rem;
  text-align: right;
  font-size: 1.2rem;
  opacity: 0.8;
  text-shadow: 0px 0px 3px #272727;
  position: static;
  text-align: left;
  margin-top: 1rem; }
  .contact span {
    line-height: 1.5rem; }
  @media (max-width: 400px) {
    .contact {
      font-size: 1.1rem; } }

.navigation {
  margin-top: auto;
  position: relative; }
  .navigation a {
    color: white;
    text-decoration: none; }
  .navigation > div, .navigation a > div {
    height: 2.7em;
    display: flex;
    opacity: 0.7;
    cursor: pointer;
    text-shadow: 0px 0px 3px #272727; }
    .navigation > div img, .navigation a > div img {
      padding: 0.5em;
      height: 100%;
      box-sizing: border-box;
      margin-left: 0em; }
    .navigation > div h3, .navigation a > div h3 {
      font-size: 2em;
      font-weight: 200;
      letter-spacing: 2px;
      display: flex;
      align-items: center;
      justify-content: flex-start; }
    .navigation > div:hover, .navigation a > div:hover {
      opacity: 1; }

.powered_by {
  position: fixed;
  bottom: 1em;
  right: 1em;
  height: 2em;
  width: auto;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  opacity: 0.7; }
  .powered_by img {
    height: 2em;
    margin-left: 1.5em; }
  @media (max-width: 700px) {
    .powered_by {
      right: auto;
      bottom: 0;
      position: relative; }
      .powered_by img {
        margin-left: 0;
        margin-right: 1.5em; } }

.skills {
  height: 100vh;
  position: relative;
  z-index: 1;
  width: auto;
  margin-left: auto;
  padding: 4em;
  padding-bottom: 2.5em;
  overflow: auto;
  box-sizing: border-box;
  background-color: #1e1e1e;
  scrollbar-color: #ff69b4 #1e1e1e;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
  align-items: flex-start;
  /* Remove for multi column */
  height: auto;
  align-content: flex-start; }
  .skills h3, .skills li {
    letter-spacing: 0.7px;
    font-weight: 200; }
  .skills li {
    opacity: 0.7; }
  .skills h3 {
    display: block;
    width: auto; }
  .skills div {
    display: inline-block;
    width: auto;
    margin: 0;
    margin: 0 2em; }
  .skills ul {
    padding: 1.5em;
    padding-right: 0em;
    padding-top: 0.5em;
    display: inline-block;
    width: auto; }
